import React, { useContext, useState, useEffect } from "react";
import Datetime from "react-datetime";
import moment from "moment-timezone";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { AuthContext } from "../../context/AuthContext";
import { useWindowWidth } from "@react-hook/window-size";
import * as uploadService from "../../services/fileUpload";
import { faCalendarAlt } from "@fortawesome/free-solid-svg-icons";
import { Col, Row, Card, Form, InputGroup } from "@themesberg/react-bootstrap";
import { securedAxiosInstance } from "../../axiosConfig";
import Loader from "../../components/Loader";
import ResumeUpload from "./components/ResumeUpload";
import { NotificationContext } from "../../context/NotificationContext";

const Profile = () => {
  const { state } = useContext(AuthContext);
  const { showNotification } = useContext(NotificationContext);

  const x = useWindowWidth();

  const [inputState, setInputState] = useState({
    Name: "",
    photo: null,
    branch: "",
    batch: "",
    email: "",
    personal_email: "",
    blood_group: "",
    academic_certificates: "",
    contact_number: "",
    perm_address: "",
    rollNumber: "",
    sem1: "",
    sem2: "",
    pan: "",
    aadhar: "",
    sem3: "",
    sem4: "",
    sem5: "",
    sem6: "",
    cgpa: "",
    dropCgpa: "",
    backlogs: "",
    bans: "",
    appliedCompaniesIntern: "",
    appliedCompaniesPlacement: "",
    resumes: null,
    resume1: null,
    resume2: null,
    resume3: null,
    dob: "",
    alt_contact_number: "",
    gender: "Male",
  });

  const [toDisable] = useState(true);

  const fetchProfileData = async () => {
    securedAxiosInstance
      .get(`/students/me`)
      .then((response) => {
        setInputState({
          Name: response.data.Name,
          photo: response.data.photo,
          branch: response.data.branch,
          degree: response.data.degree,
          batch: response.data.batch,
          email: response.data.email,
          personal_email: response.data.personal_email,
          alt_contact_number: response.data.alt_contact_number,
          gender: response.data.gender,
          blood_group: response.data.blood_group,
          academic_certificates: "",
          contact_number: response.data.contact_number,
          perm_address: response.data.perm_address,
          rollNumber: response.data.rollNumber,
          sem1: response.data?.sem1_gpa,
          sem2: response.data?.sem2_gpa,
          pan: response.data?.pan,
          aadhar: response.data?.aadhar,
          sem3: response.data?.sem3_gpa,
          sem4: response.data?.sem4_gpa,
          sem5: response.data?.sem5_gpa,
          sem6: response.data?.sem6_gpa,
          cgpa: response.data.cgpa,
          dropCgpa: response.data?.dropCgpa,
          backlogs: response.data?.backlogs,
          bans: response.data?.currentBan,
          resumes: response.data?.resume,
          resume1: response.data?.resume[0] || null,
          resume2: response.data?.resume[1] || null,
          resume3: response.data?.resume[2] || null,
          dob: response.data?.dob,
          cgpa_before_drop: response.data.cgpa_before_drop,
        });
      })
      .catch((error) => {
        console.log(error, error.response);
      });
  };

  useEffect(() => {
    fetchProfileData();
  }, [state]);

  const uploadResume = async (file, index) => {
    if (!file) {
      return;
    }

    try {
      await uploadService.uploadResumeAndLink(file, index);
    } catch (err) {
      return console.log(err);
    }

    showNotification("Resume has been uploaded");
    fetchProfileData();
  };

  const deleteResume = async (uploadId) => {
    try {
      await uploadService.deleteResume(uploadId);
    } catch (err) {
      return console.error(err);
    }

    fetchProfileData();
    showNotification("Resume has been deleted");
  };

  if (inputState.Name === "") return <Loader />;

  return (
    <div className="d-flex justify-content-center align-items-center">
      <div style={{ width: "90%" }}>
        <div>
          <h1>Your Profile</h1>
        </div>
        <Card
          style={{ flexDirection: "row" }}
          border="light"
          className="bg-white shadow-sm p-4"
        >
          <Card.Body>
            <Card.Title className="mb-1">{inputState.Name}</Card.Title>
            <Card.Text>
              {inputState.branch}| {inputState.batch}
            </Card.Text>
          </Card.Body>
        </Card>
        <h4 className="mt-3 mb-1 mx-2">Personal</h4>
        <Form>
          <Card className="p-4">
            <Row>
              <Col md={6} className="mb-3">
                <Form.Group id="batch">
                  <Form.Label style={{ fontSize: "1rem" }}>Name</Form.Label>
                  <Form.Control
                    style={
                      x > 1200
                        ? { width: "75%", float: "right" }
                        : { width: "100" }
                    }
                    className="mr-2"
                    required
                    type="text"
                    name="batch"
                    value={inputState.Name}
                    placeholder={inputState.Name}
                    disabled={true}
                  />
                </Form.Group>
              </Col>
              <Col md={6} className="mb-3">
                <Form.Group id="blood_group">
                  <Form.Label style={{ fontSize: "1rem" }}>
                    Blood Group
                  </Form.Label>
                  <Form.Control
                    style={
                      x > 1200
                        ? { width: "75%", float: "right" }
                        : { width: "100" }
                    }
                    className="mr-2"
                    required
                    type="text"
                    name="blood_group"
                    value={inputState.blood_group}
                    placeholder={inputState.blood_group}
                    disabled={true}
                  />
                </Form.Group>
              </Col>
            </Row>
            <Row>
              <Col md={6} className="mb-3">
                <Form.Group>
                  <Form.Label className="mt-2">Date of Birth : </Form.Label>
                  <div
                    style={x > 1200 ? { width: "75%", float: "right" } : {}}
                    className="mr-2"
                  >
                    <Datetime
                      timeFormat={false}
                      closeOnSelect={false}
                      renderInput={(props, openCalendar) => (
                        <InputGroup>
                          <InputGroup.Text>
                            <FontAwesomeIcon icon={faCalendarAlt} />
                          </InputGroup.Text>
                          <Form.Control
                            required
                            type="text"
                            value={
                              inputState.dob
                                ? moment(inputState.dob).format("MM/DD/YYYY")
                                : ""
                            }
                            placeholder={
                              inputState.dob
                                ? moment(inputState.dob).format("MM/DD/YYYY")
                                : ""
                            }
                            onFocus={openCalendar}
                            onChange={() => {}}
                            disabled={true}
                          />
                        </InputGroup>
                      )}
                    />
                  </div>
                </Form.Group>
              </Col>
              <Col md={6} className="mb-3">
                <Form.Group id="gender">
                  <Form.Label>Gender</Form.Label>
                  <Form.Control
                    style={x > 1200 ? { width: "75%", float: "right" } : {}}
                    // required4
                    disabled
                    className="mr-2"
                    type="text"
                    name="gender"
                    value={inputState.gender}
                    //
                    placeholder={inputState.gender}
                  />
                </Form.Group>
              </Col>
              <Col md={6} className="mb-3">
                <Form.Group id="batch">
                  <Form.Label>Batch</Form.Label>
                  <Form.Control
                    style={x > 1200 ? { width: "75%", float: "right" } : {}}
                    // required4
                    disabled
                    className="mr-2"
                    type="text"
                    name="batch"
                    value={inputState.batch}
                    //
                    placeholder={inputState.batch}
                  />
                </Form.Group>
              </Col>
              <Col md={6} className="mb-3">
                <Form.Group id="degree">
                  <Form.Label>Degree</Form.Label>
                  <Form.Control
                    style={x > 1200 ? { width: "75%", float: "right" } : {}}
                    // required4
                    disabled
                    className="mr-2"
                    type="text"
                    name="degree"
                    value={inputState.degree}
                    placeholder={inputState.degree}
                  />
                </Form.Group>
              </Col>
              <Col md={12} className="mb-3">
                <Form.Group id="email">
                  <Form.Label>Email</Form.Label>
                  <Form.Control
                    style={x > 1200 ? { width: "75%", float: "right" } : {}}
                    // required
                    disabled
                    className="mr-2"
                    name="email"
                    value={inputState.email}
                    type="email"
                    placeholder={inputState.email}
                  />
                </Form.Group>
              </Col>
              <Col md={12} className="mb-3">
                <Form.Group id="email">
                  <Form.Label>Personal Email</Form.Label>
                  <Form.Control
                    style={x > 1200 ? { width: "75%", float: "right" } : {}}
                    className="mr-2"
                    name="personal_email"
                    value={inputState.personal_email}
                    type="email"
                    placeholder={inputState.personal_email}
                    disabled
                  />
                </Form.Group>
              </Col>
            </Row>
            <Row>
              <Col md={12} className="mb-3">
                <Form.Group id="roll-number">
                  <Form.Label>Roll Number</Form.Label>
                  <Form.Control
                    // required
                    disabled
                    style={x > 1200 ? { width: "75%", float: "right" } : {}}
                    type="text"
                    className="mr-2"
                    name="rollNumber"
                    value={inputState.rollNumber}
                    //
                    placeholder={inputState.rollNumber}
                  />
                </Form.Group>
              </Col>
              <Col md={12} className="mb-3">
                <Form.Group id="branch">
                  <Form.Label>Branch{x > 1200 ? <br /> : " "}</Form.Label>
                  <Form.Select
                    style={x > 1200 ? { width: "75%", float: "right" } : {}}
                    name="branch"
                    className="mr-2"
                    disabled
                    //
                    value={inputState.branch && inputState.branch}
                    placeholder={inputState.branch && inputState.branch}
                  >
                    <option defaultValue value="COE">
                      COE
                    </option>
                    <option value="CSAI">CSAI</option>
                    <option value="IT">IT</option>
                    <option value="MAC">MAC</option>
                    <option value="ECE">ECE</option>
                    <option value="ICE">ICE</option>
                    <option value="EEE">EEE</option>
                    <option value="ME">ME</option>
                    <option value="MPAE">MPAE</option>
                    <option value="BT">BT</option>
                    <option value="BBA">BBA</option>
                  </Form.Select>
                </Form.Group>
              </Col>
            </Row>
            <Row>
              <Col md={12} className="mb-3">
                <Form.Group id="contact_num">
                  <Form.Label>Contact Number</Form.Label>
                  <Form.Control
                    style={x > 1200 ? { width: "75%", float: "right" } : {}}
                    // required
                    className="mr-2"
                    name="contact_number"
                    disabled
                    value={inputState.contact_number}
                    type="text"
                    placeholder={inputState.contact_number}
                  />
                </Form.Group>
              </Col>
              <Col md={12} className="mb-3">
                <Form.Group id="contact_num">
                  <Form.Label>Alt. Contact No.</Form.Label>
                  <Form.Control
                    style={x > 1200 ? { width: "75%", float: "right" } : {}}
                    // required
                    className="mr-2"
                    name="alt_contact_number"
                    disabled
                    value={inputState.alt_contact_number}
                    type="text"
                    placeholder={inputState.alt_contact_number}
                  />
                </Form.Group>
              </Col>
              <Col md={12} className="mb-3">
                <Form.Group id="perm_address">
                  <Form.Label>Permanent Address</Form.Label>
                  <Form.Control
                    // required
                    style={
                      x > 1200
                        ? { width: "75%", float: "right", mcursor: "no-drop" }
                        : {}
                    }
                    name="perm_address"
                    disabled
                    className="mr-2"
                    value={inputState.perm_address}
                    type="text"
                    placeholder={inputState.perm_address}
                  />
                </Form.Group>
              </Col>
              <Col md={6} className="mb-3">
                <Form.Group id="perm_address">
                  <Form.Label>Aadhaar</Form.Label>
                  <Form.Control
                    // required
                    style={
                      x > 1200
                        ? { width: "75%", float: "right", mcursor: "no-drop" }
                        : {}
                    }
                    name="aadhar"
                    disabled
                    className="mr-2"
                    value={inputState.aadhar}
                    type="text"
                    placeholder={inputState.aadhar}
                  />
                </Form.Group>
              </Col>
              <Col md={6} className="mb-3">
                <Form.Group id="perm_address">
                  <Form.Label>PAN Card</Form.Label>
                  <Form.Control
                    // required
                    style={
                      x > 1200
                        ? { width: "75%", float: "right", mcursor: "no-drop" }
                        : {}
                    }
                    name="pan"
                    disabled
                    className="mr-2"
                    value={inputState.pan}
                    type="text"
                    placeholder={inputState.pan}
                  />
                </Form.Group>
              </Col>
            </Row>
          </Card>
          <h4 className="mt-3 mb-1 mx-2">Academics</h4>
          <Card className="p-4">
            <Row>
              <Col lg={6} className="mb-3">
                <Form.Group id="gpa-1">
                  <Form.Label class="mt-2">Sem 1 GPA</Form.Label>
                  <Form.Control
                    style={
                      x > 500
                        ? { width: "75%", float: "right", mcursor: "no-drop" }
                        : { mcursor: "no-drop" }
                    }
                    className="mr-2"
                    disabled={toDisable}
                    type="text"
                    name="sem1"
                    required
                    value={inputState.sem1}
                    placeholder={inputState.sem1}
                  />
                </Form.Group>
              </Col>
              <Col lg={6} className="mb-3">
                <Form.Group id="gpa-2">
                  <Form.Label class="mt-2">Sem 2 GPA</Form.Label>
                  <Form.Control
                    required
                    disabled={toDisable}
                    style={
                      x > 500
                        ? { width: "75%", float: "right", mcursor: "no-drop" }
                        : { mcursor: "no-drop" }
                    }
                    type="text"
                    className="mr-2"
                    name="sem2"
                    value={inputState.sem2}
                    placeholder={inputState.sem2}
                  />
                </Form.Group>
              </Col>
            </Row>
            <Row>
              <Col lg={6} className="mb-3">
                <Form.Group id="gpa-3">
                  <Form.Label class="mt-2">Sem 3 GPA</Form.Label>
                  <Form.Control
                    style={
                      x > 500
                        ? { width: "75%", float: "right", mcursor: "no-drop" }
                        : { mcursor: "no-drop" }
                    }
                    required
                    className="mr-2"
                    disabled={toDisable}
                    type="text"
                    name="sem3"
                    value={inputState.sem3}
                    placeholder={inputState.sem3}
                  />
                </Form.Group>
              </Col>
              <Col lg={6} className="mb-3">
                <Form.Group id="gpa-4">
                  <Form.Label class="mt-2">Sem 4 GPA</Form.Label>
                  <Form.Control
                    required
                    style={
                      x > 500
                        ? { width: "75%", float: "right", mcursor: "no-drop" }
                        : { mcursor: "no-drop" }
                    }
                    className="mr-2"
                    type="text"
                    disabled={toDisable}
                    name="sem4"
                    value={inputState.sem4}
                    placeholder={inputState.sem4}
                  />
                </Form.Group>
              </Col>
            </Row>
            <Row>
              <Col lg={6} className="mb-3">
                <Form.Group id="gpa-5">
                  <Form.Label class="mt-2">Sem 5 GPA</Form.Label>
                  <Form.Control
                    style={
                      x > 500
                        ? { width: "75%", float: "right", mcursor: "no-drop" }
                        : { mcursor: "no-drop" }
                    }
                    className="mr-2"
                    required
                    disabled={toDisable}
                    type="text"
                    name="sem5"
                    value={inputState.sem5}
                    placeholder={inputState.sem5}
                  />
                </Form.Group>
              </Col>
              <Col lg={6} className="mb-3">
                <Form.Group id="gpa-6">
                  <Form.Label class="mt-2">Sem 6 GPA</Form.Label>
                  <Form.Control
                    style={
                      x > 500
                        ? { width: "75%", float: "right", mcursor: "no-drop" }
                        : { mcursor: "no-drop" }
                    }
                    className="mr-2"
                    required
                    disabled={toDisable}
                    type="text"
                    name="sem6"
                    value={inputState.sem6}
                    placeholder={inputState.sem6}
                  />
                </Form.Group>
              </Col>
            </Row>

            <Row>
              <Col md={12} className="mb-3">
                <Form.Group id="cgpa">
                  <Form.Label>Final CGPA</Form.Label>
                  <Form.Control
                    style={
                      x > 500
                        ? { width: "75%", float: "right", mcursor: "no-drop" }
                        : { mcursor: "no-drop" }
                    }
                    className="mr-2"
                    required
                    disabled={toDisable}
                    type="text"
                    name="cgpa"
                    value={inputState.cgpa}
                    placeholder={inputState.cgpa}
                  />
                </Form.Group>
              </Col>
              <Col md={12} className="mb-3">
                <Form.Group id="cgpa-drop">
                  <Form.Label>CGPA - Before Drop</Form.Label>
                  <Form.Control
                    disabled={toDisable}
                    style={
                      x > 500
                        ? { width: "75%", float: "right", mcursor: "no-drop" }
                        : { mcursor: "no-drop" }
                    }
                    required
                    className="mr-2"
                    type="text"
                    name="dropCgpa"
                    value={inputState.cgpa_before_drop}
                    placeholder={inputState.cgpa_before_drop}
                  />
                </Form.Group>
              </Col>
            </Row>
            <Row>
              <Col md={12} className="mb-3">
                <Form.Group id="backlogs">
                  <Form.Label>Backlogs</Form.Label>
                  <Form.Control
                    style={
                      x > 500
                        ? { width: "75%", float: "right", mcursor: "no-drop" }
                        : { mcursor: "no-drop" }
                    }
                    disabled={toDisable}
                    className="mr-2"
                    required
                    type="text"
                    name="backlogs"
                    value={inputState.backlogs}
                    placeholder={inputState.backlogs}
                  />
                </Form.Group>
              </Col>
              <Col md={12} className="mb-3">
                <Form.Group id="bans">
                  <Form.Label>Bans</Form.Label>
                  <Form.Control
                    disabled
                    required
                    style={
                      x > 500
                        ? { width: "75%", float: "right", mcursor: "no-drop" }
                        : { mcursor: "no-drop" }
                    }
                    type="text"
                    name="bans"
                    className="mr-2"
                    value={inputState.bans}
                    placeholder={inputState.bans}
                  />
                </Form.Group>
              </Col>
            </Row>
          </Card>
          <div className="mt-3">
            <ResumeUpload
              resumes={inputState.resumes}
              uploadFn={uploadResume}
              deleteFn={deleteResume}
            />
          </div>
        </Form>
      </div>
    </div>
  );
};

export default Profile;
